<script>
export default {
  name: "HomePage",
  data() {
    return {
      navBackgroundColor: '#001628',
      primaryColor: '#EB6A3F',
      active: '0',  // 当前激活的导航索引,
      screenWidth: document.body.clientWidth,
      screenHeight: document.documentElement.clientHeight,
      cardDiameter: "190px",
      cardDiameter2: "220px",
      iconDiameter: 50,
    }
  },
  created(){
    document.documentElement.scrollTop = 0
  },
  mounted() {
    this.onScroll()
    // 监听滚动事件

    window.addEventListener('scroll', this.onScroll)

    // const diameter = (this.screenWidth < this. screenHeight? this.screenWidth : this.screenHeight) * 0.3
    // this.cardDiameter = diameter + 'px'
    // this.iconDiameter = diameter * 0.25
    console.log('v1.2')
  },
  destroyed() {
    // 移除监听器
    window.removeEventListener('scroll', this.onScroll)
  },
  methods: {
    onScroll() {
      // 获取所有锚点元素
      const navContents = document.querySelectorAll('.el-divider')
      // 所有锚点元素的 offsetTop
      const offsetTopArr = [0]
      navContents.forEach(item => {
        offsetTopArr.push(item.offsetTop)
      })
      // 获取当前的 scrollTop
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      const scrollHeight = document.body.scrollHeight
      const windowHeight = document.documentElement.clientHeight


      // 定义当前点亮的导航下标
      let navIndex = 0
      if (scrollTop + windowHeight >= scrollHeight) {
        navIndex = 5
      } else {
        for (let n = 0; n < offsetTopArr.length; n++) {
          // 如果 scrollTop 大于等于第 n 个元素的 offsetTop 则说明 n-1 的内容已经完全不可见
          // 那么此时导航索引就应该是 n 了
          if (scrollTop + 65 >= offsetTopArr[n]) {
            navIndex = n
          }
        }
      }
      // 把下标赋值给 vue 的 data
      this.active = String(navIndex)
    },
    scrollTo(index) {
      // 获取所有锚点元素
      const navContents = document.querySelectorAll('.el-divider')
      // 所有锚点元素的 offsetTop
      const offsetTopArr = [0]
      navContents.forEach(item => {
        offsetTopArr.push(item.offsetTop)
      })
      // 获取当前的 scrollTop
      var scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      // 获取目标位置的 offsetTop
      const targetOffsetTop = offsetTopArr[index] - 65

      // 定义一次跳 STEP 个像素
      const STEP = 50
      // 判断是往下滑还是往上滑
      if (scrollTop > targetOffsetTop) {
        smoothUp()
      } else {
        smoothDown()
      }
      // 定义往下滑函数
      function smoothDown() {
        // 如果当前 scrollTop 小于 targetOffsetTop 说明视口还没滑到指定位置
        if (scrollTop < targetOffsetTop) {
          // 如果和目标相差距离大于等于 STEP 就跳 STEP
          // 否则直接跳到目标点，目标是为了防止跳过了。
          if (targetOffsetTop - scrollTop >= STEP) {
            scrollTop += STEP
          } else {
            scrollTop = targetOffsetTop
          }
          document.body.scrollTop = scrollTop
          document.documentElement.scrollTop = scrollTop
          requestAnimationFrame(smoothDown)
        }
      }
      // 定义往上滑函数
      function smoothUp() {
        if (scrollTop > targetOffsetTop) {
          if (scrollTop - targetOffsetTop >= STEP) {
            scrollTop -= STEP
          } else {
            scrollTop = targetOffsetTop
          }
          document.body.scrollTop = scrollTop
          document.documentElement.scrollTop = scrollTop
          requestAnimationFrame(smoothUp)
        }
      }
    },
    navigateToNewPage(newpage) {
      switch (newpage){
        case 'kellect4apt': this.$router.push({ name: 'Kellect4APTDataset' }); break;
        //case 'maliciousdataset': this.$router.push({ name: 'MaliciousDataset' }); break;
      }
    }
  }
}

</script>

